// Careful, this component does not have a shadow root
@import "../../../node_modules/codemirror/lib/codemirror";
@import "../../../node_modules/codemirror/theme/monokai";

@import "stylesheets/sdx/variables/global";
@import "stylesheets/sdx/variables/colors";
@import "stylesheets/sdx/variables/sizes";

sdx-code-editor {
  display: block;

  .preview {
    border: 1px solid $color-gray-tint-8;
    border-radius: $border-radius-large $border-radius-large 0 0;
    padding: $baseline-3;

    ~ .CodeMirror {
      border-radius: 0 0 $border-radius-large $border-radius-large;
    }
  }

  &.borderless .preview {
    border: 0;
    padding-left: 0;
    padding-right: 0;
  }

  &.readonly {
    .CodeMirror {
      &:after {
        content: "Code";
      }
    }
  }
}

.CodeMirror {
  height: auto;
  font-family: Monaco, monospace;
  font-size: 14px;
  padding: $baseline-2 * 3 $baseline-3 $baseline-3;
  border-radius: $border-radius-large;

  &:after {
    content: "Playground";
    position: absolute;
    top: 0;
    left: 0;
    color: $color-gray-tint-4;
    border-right: 1px solid $color-gray-tint-4;
    border-bottom: 1px solid $color-gray-tint-4;
    border-bottom-right-radius: $baseline * 0.5;
    padding: $baseline * 0.5 $baseline;
  }
}

.CodeMirror-line span.cm-tag {
  color: $color-pink-tint-5; //overwrite origin with accessible color
}

.sdx--dark-theme {
  .preview {
    border-color: $color-dusk-tint-4-dark-theme;
  }

  .CodeMirror-line .cm-comment {
    color: $color-gray-tint-5-dark-theme;
  }
}
