import { Component, h, Element, Prop, Host, Method } from "@stencil/core"
import CodeMirror from "codemirror"
import "codemirror/mode/htmlmixed/htmlmixed"
import { dispatchEvent } from "../../core/helpers/webcomponent-helpers"

@Component({
  tag: "sdx-code-editor",
  styleUrl: "code-editor.scss",
  shadow: false, // polyfill fix (elements are created by CodeMirror - without the scope class name)
})
export class CodeEditor {
  private hasBeenRefreshedOnce = false
  private previewEl?: HTMLDivElement
  private textAreaEl?: HTMLTextAreaElement
  private editor?: CodeMirror.EditorFromTextArea

  @Element() public el!: HTMLSdxInputItemElement

  @Prop() public language: string = "htmlmixed"

  // innerHTML security: do not use preview if the input method of the value is
  // not safe
  @Prop() public preview: boolean = true

  @Prop() public readonly: boolean = false

  @Method()
  public async refresh() {
    this.editor!.refresh()
  }

  @Method()
  public async refreshOnce() {
    if (this.hasBeenRefreshedOnce) {
      return
    }

    this.refresh()
    this.hasBeenRefreshedOnce = true
  }

  public componentWillLoad() {
    this.textAreaEl = this.el.querySelector("textarea") ?? undefined

    if (!this.textAreaEl) {
      return
    }

    // innerHTML security: entities of a textarea are being decoded by default, so no script tag will be executed here
    this.textAreaEl.innerHTML = this.indent(this.textAreaEl.innerHTML)
  }

  public componentDidLoad() {
    if (!this.textAreaEl) {
      return
    }

    const readOnly = this.readonly || !this.preview

    this.editor = CodeMirror.fromTextArea(this.textAreaEl, {
      mode: this.language,
      theme: "monokai",
      readOnly,
      ...(readOnly ? { cursorBlinkRate: -1 } : {}),
    })

    this.editor.on("change", () => this.onChange())
    this.renderPreview()
  }

  private onChange() {
    this.editor!.save()
    this.renderPreview()
  }

  private renderPreview() {
    if (this.previewEl) {
      // innerHTML security: the value is not sanitized
      this.previewEl.innerHTML = this.editor!.getValue()
      dispatchEvent(this.el, "sdxrenderpreview")
    }
  }

  // Indent and trim blank lines
  private indent(text: string): string {
    const textLines = text.split("\n")

    // Remove blank lines at the beginning
    while (textLines[0].trim() === "") {
      textLines.splice(0, 1)
    }

    // Remove blank lines at the end
    while (textLines[textLines.length - 1].trim() === "") {
      textLines.splice(textLines.length - 1, 1)
    }

    // Detect indentation (count leading whitespaces on first line)
    const indentation = textLines[0].search(/\S/)

    // Indent code
    for (let i = 0; i < textLines.length; i++) {
      textLines[i] = textLines[i].substring(indentation)
    }

    return textLines.join("\n")
  }

  private getHostClassNames() {
    return {
      paragraph: true,
      readonly: this.readonly || !this.preview,
    }
  }

  public render() {
    return (
      <Host class={this.getHostClassNames()}>
        {this.preview && (
          <div class="preview" ref={(el) => (this.previewEl = el)}></div>
        )}

        <slot />
      </Host>
    )
  }
}
